import { Component } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';

import Issue from './Issue';
import Sidebar from './Sidebar';

import WrapperStyles from './../styles/WrapperStyles';

// a little function to help us with reordering the result
const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

const getItemStyle = (isDragging, draggableStyle) => ({
  userSelect: 'none',
  background: isDragging ? '#5582fa' : '#eee',
  margin: '1rem',
  ...draggableStyle
});

export default class Drag extends Component {
  state = {
    isLoaded: false,
    items: [],
    username: '',
    token: ''
  };

  onDragEnd = result => {
    if (!result.destination) {
      return;
    }

    const items = reorder(this.state.items, result.source.index, result.destination.index);

    this.setState({
      items
    });
  };

  getIssues = () => {
    fetch(`https://api.github.com/search/issues?q=${this.state.username}`, {
      headers: {
        Authorization: `token ${this.state.token}`
      }
    })
      .then(response => response.json())
      .then(data => {
        console.log(data.items);
        this.setState({ isLoaded: true, items: data.items });
      })
      .catch(error => {
        console.error(error);
        this.setState({ isLoaded: true, error });
      });
  };

  componentDidMount() {
    this.getIssues();
  }

  componentWillReceiveProps(prevProps) {
    if (prevProps === undefined) {
      return false;
    }
    this.setState({
      isLoaded: true
    });
    this.getIssues(prevProps);
  }

  setForm = sidebarState => {
    const { token, username } = sidebarState;
    this.setState({ token, username, error: false });
    setTimeout(() => this.getIssues(), 0);
  };

  render() {
    return (
      <WrapperStyles>
        <Sidebar getForm={this.setForm} />
        <DragDropContext onDragEnd={this.onDragEnd}>
          <Droppable droppableId="droppable">
            {provided => (
              <div {...provided.droppableProps} ref={provided.innerRef}>
                {this.state.items &&
                  this.state.items.map((issue, index) => (
                    <Draggable key={issue.id} draggableId={issue.id} index={index}>
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(snapshot.isDragging, provided.draggableProps.style)}
                        >
                          <Issue issue={issue} />
                        </div>
                      )}
                    </Draggable>
                  ))}
                {provided.placeholder}
              </div>
            )}
          </Droppable>
        </DragDropContext>
      </WrapperStyles>
    );
  }
}
